\n      TIME_BOX_STYLE: {\n        flexDirection: 'row',\n        justifyContent: 'center',\n        alignItems: 'center',\n        backgroundColor: '#333333',\n        height: '30px',\n        width: '30px'\n      },\n      DOT_BOX_STYLE: {\n        width: '18px',\n        flexDirection: 'row',\n        justifyContent: 'center',\n        alignItems: 'center'\n      },\n      TIME_TEXT_STYLE: {\n        color: '#FFCC80',\n        fontSize: '18px'\n      },\n      DOT_TEXT_STYLE: {\n        color: '#333333',\n        fontSize: '18px',\n        fontWeight: 'bold'\n      }\n    }),\n    mounted () {\n      setInterval(() => {\n        this.NOW_DATE = new Date().getTime();\n      }, this.interval)\n    },\n    computed: {\n      mrTimeWrapStyle () {\n        return {\n          ...this.TIME_WRAP_STYLE,\n          ...this.timeWrapStyle\n        }\n      },\n      mrTimeBoxStyle () {\n        return {\n          ...this.TIME_BOX_STYLE,\n          ...this.timeBoxStyle\n        }\n      },\n      mrDotBoxStyle () {\n        return {\n          ...this.DOT_BOX_STYLE,\n          ...this.dotBoxStyle\n        }\n      },\n      mrTimeTextStyle () {\n        return {\n          ...this.TIME_TEXT_STYLE,\n          ...this.timeTextStyle\n        }\n      },\n      mrDotTextStyle () {\n        return {\n          ...this.DOT_TEXT_STYLE,\n          ...this.dotTextStyle\n        }\n      },\n\n      countDownData () {\n        const timeSpacing = this.time - this.NOW_DATE;\n\n        // 倒计时结束了\n        if (timeSpacing < 0) {\n          if (this.completed === false) {\n            this.$emit('wxcOnComplete');\n          }\n          this.completed = true;\n          return {\n            hour: '00',\n            minute: '00',\n            second: '00'\n          }\n        }\n\n        // 计算小时\n        const hours = Math.floor(timeSpacing / (3600 * 1000));\n\n        // 计算分钟(去除小时)\n        const minute = Math.floor(timeSpacing % (3600 * 1000) / (60 * 1000));\n\n        // 计算秒数(去除分钟)\n        const second = Math.floor(timeSpacing % (60 * 1000) / 1000);\n\n        return {\n          hour: hours < 10 ? '0' + hours : hours,\n          minute: minute < 10 ? '0' + minute : minute,\n          second: second < 10 ? '0' + second : second\n        }\n      }\n    },\n\n    methods: {\n      // 分析模板\n      tplObj () {\n        const tplIndexOfHours = this.tpl.indexOf('h');\n        const tplIndexOfMinutes = this.tpl.indexOf('m');\n        const tplIndexOfSeconds = this.tpl.indexOf('s');\n\n        return {\n          firstDot: this.tpl.slice(tplIndexOfHours + 2, tplIndexOfMinutes - 1),\n          secondDot: this.tpl.slice(tplIndexOfMinutes + 2, tplIndexOfSeconds - 1)\n        }\n      }\n    }\n  }\n</script>\n"],"sourceRoot":""}]);

